.result {
  background: rgb(248, 248, 248);
  -webkit-animation: spread-in 1s forwards;
          animation: spread-in 1s forwards;
  padding: 2em 3em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow-x: visible;
  overflow-y: auto;
}
@-webkit-keyframes spread-in {
  from {
    height: 0;
  }
  to {
    height: calc(100vh - 155px);
  }
}
@keyframes spread-in {
  from {
    height: 0;
  }
  to {
    height: calc(100vh - 155px);
  }
}
.toolbar input {
  width: 15px;
  height: 15px;
  padding: 0;
}
.toolbar div {
  border: 0.1px solid rgba(0, 0, 0, 0.1);
  display: inline-block;
  padding: 10px 15px;
  margin-right: 15px;
  margin-bottom: 30px;
}



